import {
  addToAPI,
  getDetailFromAPI,
  getFromAPI,
  removeFromAPI,
  updateInAPI,
  clearCollectionInAPI
} from './dbAPI.js'

const baseState = {
  movieCollection: {},
  totalSpent: 0,
  suggestedToday: {
    date: null,
    id: ''
  },
  stores: {
    'amazon': 'Amazon',
    'elcorteingles': 'El corte Inglés',
    'fnac': 'Fnac',
    'cex': 'Cex',
    'carrefour': 'Carrefour',
    'selectavision': 'Selecta Visión',
    'mediamarkt': 'Mediamarkt',
    'game': 'Game',
    'centromail': 'Centro Mail'
  },
  formats: {
    'br': 'Bluray',
    'dvd': 'DVD'
  }
}

const init = function (e) {
  getAllFromDB().then((collection) => {
    baseState.movieCollection = collection
    // No pelis, no party
    if (!collection.length) return false
    // Calcular el total invertido
    baseState.totalSpent = recalculateTotalSpent(baseState)
    // Miramos si la que hay guardada en el localStorage es la sugerida de hoy
    const suggestedFromLocal = getFromLocalStorage('config.suggestedToday')

    if (suggestedFromLocal && suggestedFromLocal.id && isToday(suggestedFromLocal.date)) {

      // Antes de pedir detalle a la API vamos a comprobar que existe en la colección
      if (!collection.some(movie => movie.id === suggestedFromLocal.id)) {
        // Si no existe generamos una nueva
        const suggestedTodayNew = getSmartDailyMovie(collection)
        baseState.suggestedToday = suggestedTodayNew
        setToLocalStorage('config.suggestedToday', suggestedTodayNew)
        return;
      }

      getFromDB(suggestedFromLocal.id)
        .then(item => {
          if (item) {
            baseState.suggestedToday = {
              date: item.date,
              id: item.id
            }
          } else {
            const suggestedTodayNew = getSmartDailyMovie(collection)
            baseState.suggestedToday = suggestedTodayNew
            setToLocalStorage('config.suggestedToday', suggestedTodayNew)
          }
        }
      )
      .catch(e => {
        const suggestedTodayNew = getSmartDailyMovie(collection)
        baseState.suggestedToday = suggestedTodayNew
        setToLocalStorage('config.suggestedToday', suggestedTodayNew)
      })

    } else {
      const suggestedTodayNew = getSmartDailyMovie(collection)
      baseState.suggestedToday = suggestedTodayNew
      setToLocalStorage('config.suggestedToday', suggestedTodayNew)
    }

  }).catch(console.error)
}

export const getSmartDailyMovie = collection => {
    const today = new Date().getUTCDate() // Cambia cada día
    const month = new Date().getUTCMonth()
    
    // 1. Ordenar la colección para que el índice sea predecible
    // (Importante: .sort() afecta al array original, mejor usa una copia)
    const sortedCollection = [...collection].sort((a, b) => a.id.localeCompare(b.id))

    // 2. Filtrar preferiblemente las no vistas
    const unwatched = sortedCollection.filter(m => !m.watched)
    const targetList = unwatched.length > 0 ? unwatched : sortedCollection

    // 3. Selección determinista basada en la fecha (día + mes para variar cada año)
    const seed = today + month
    const selectedMovie = targetList[seed % targetList.length]

    return {
        date: Date.now(),
        id: selectedMovie.id,
        isNew: !selectedMovie.watched // Meta-información útil
    }
}

export const recalculateTotalSpent = state => {
  return state.movieCollection.reduce((acc, item) => {
    acc += +item.cost
    return acc
  }, 0)
}

export const random = (min, max) => {
  return Math.round(min + Math.random() * (max - min))
}

export const isToday = time => {
  const date = new Date(+time).toLocaleString().split(' ').shift()
  const today = new Date().toLocaleString().split(' ').shift()
  return date === today
}

export const addToDB = async data => await addToAPI(data)

export const getFromDB = async id => await getDetailFromAPI(id)

export const removeFromDB = async id => await removeFromAPI(id)

export const updateFromDB = async (id, data) => await updateInAPI(id, data)

export const getAllFromDB = () => new Promise((resolve, reject) => {
  getFromAPI().then(list => {
    list = list.map(m => {
      m.addDate = new Date(m.addDate)
      return m
    })
    resolve(list)
  }).catch(reject)
})

export const clearDB = () => new Promise((resolve, reject) => {
  clearCollectionInAPI()
  .then(resolve)
  .catch(reject)
})

